 import React from 'react';

import Container from 'react-bootstrap/Container';
import Dropdown from 'react-bootstrap/Dropdown';
import DropdownButton from 'react-bootstrap/DropdownButton';
import FormControl from 'react-bootstrap/FormControl';
import InputGroup from 'react-bootstrap/InputGroup';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch } from '@fortawesome/free-solid-svg-icons';

import Resource from "../components/Resource";

import { Link } from 'react-router-dom';

const Regions = require('../constants/regions.json');
const Resources = require('../constants/resources.json');

const capitalize = (s) => {
  if (typeof s !== 'string') return ''
  return s.charAt(0).toUpperCase() + s.slice(1)
}



const styles = {
  container: {
    backgroundColor: 'white',
    border:'2px solid #ececec',
    borderRadius: 4,
    marginTop: 20,
    paddingBottom: 12,
    paddingTop: 12
  }
}

class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      category: null,
      locale: "en",
      regionId: null,
      searchTerm: ""
    }
  }

  onCategorySelected = (category) => {
    this.setState({
      category: category == "_all_" ? null : category
    });
  };

  onRegionSelected = (regionId) => {
    this.setState({
      regionId: regionId == "_all_" ? null : regionId
    });
  };

  handleSearchTermChanged = (event) => {
    this.setState({
      searchTerm: event.target.value
    });
  };

  filterResources = (resources) => {

    let filteredResources = [...resources];
    const searchTerm = this.state.searchTerm.toUpperCase();

    filteredResources = filteredResources.filter(resource => {
      if (this.state.regionId && this.state.regionId != resource.regionId) {
        return false;
      }
      if (this.state.category && this.state.category != resource.category) {
        return false;
      }
      if (!searchTerm || searchTerm === "") {
        return true;
      }
      const description = resource.description[this.state.locale];
      return resource.name.toUpperCase().indexOf(searchTerm) > -1 
            || (description && description.toUpperCase().indexOf(searchTerm) > -1)
            || (resource.city && resource.city.toUpperCase().indexOf(searchTerm) > -1)
            || (resource.category && resource.category.toUpperCase().indexOf(searchTerm)> - 1)
            || (resource.subCategory && resource.subCategory.toUpperCase().indexOf(searchTerm)> - 1);
    });

    return filteredResources;
  }

  render() {
    const resources = this.filterResources(Resources);
    return (
      <div className="App">
        <Navbar bg="primary" variant="dark" expand="lg">
          <Navbar.Brand href="/">Ceredigion Resources</Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav 
              activeKey={this.state.regionId} 
              className="mr-auto"
              onSelect={this.onRegionSelected}
            >
              <Nav.Link
                active={!this.state.regionId}
                eventKey={"_all_"}
              >
              All
              </Nav.Link>
              {Regions.map((region, index) => {
                return (
                  <Nav.Link
                    key={index}
                    eventKey={region.id}
                  >
                    {region.name}
                  </Nav.Link>
                )
              })}
            </Nav>
          </Navbar.Collapse>
        </Navbar>
        <Container style={styles.container} fluid="md">
          <InputGroup>
            <InputGroup.Prepend>
              <InputGroup.Text>
                <FontAwesomeIcon icon={faSearch}/>
              </InputGroup.Text>
            </InputGroup.Prepend>
            <FormControl value={this.state.searchTerm} onChange={this.handleSearchTermChanged} size="lg" type="text" placeholder="Search by name, town, or category" />
            <DropdownButton
              as={InputGroup.Append}
              onSelect={this.onCategorySelected}
              title={!!this.state.category ? capitalize(this.state.category) : "All"}
              variant={"secondary"}
            >
              <Dropdown.Item eventKey={"_all_"}>All</Dropdown.Item>
              <Dropdown.Item eventKey={"childcare"}>Childcare</Dropdown.Item>
              <Dropdown.Item eventKey={"food"}>Food</Dropdown.Item>
            </DropdownButton>
          </InputGroup>
        </Container>
        <Container fluid="md" style={{marginTop: 22}}>
          {resources.map((resource, index) => {
            return (
              <Resource
                key={index}
                locale={this.state.locale}
                resource={resource}
                style={{marginBottom: 24}}
              />
            )
          })
        }
        </Container>
      </div>
    );
  }
}

export default App;
